.signup-btn {
    background: #BD220B;
    border-radius: 4px;
    height: 35px;
    width: 102px;
    border: none;
    color:#fff;
    font-weight: bold;
}

.signin-btn {
    background-color: inherit;
    color: #fff;
    font-weight: bold;
    border: none;
}
.card-grid {
    display: grid; 
    grid-template-columns: repeat(4, 1fr);
    padding: 120px;
    background-color: #111111;
    
}
.movie-container {
    width: 250px;
    height: 444px;
    border-radius: 10px;
    background-color: inherit;
    color: #fff;

}
.movie-poster img {
    width: 100%;
    height: 376px;
    border-radius:10px;
}
.movie-poster {
    position: relative;
}
.movie-poster-overlay {
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: none;
}
.movie-poster-overlay img {
    width: 200px;
    height: 200px;
    position: absolute;
    top: 80px;
    left: 25px;

}
.movie-poster:hover .movie-poster-overlay {
    display: block;
}
.movie-title {
width: 233px;
height: 14px;
font-family: SF Pro Display;
font-style: normal;
font-weight: bold;
font-size: 24px;
line-height: 24px;
color: #FFFFFF;
padding: 15px 0 16px 0;
}
.movie-container-footer ul {
    display: flex;
    justify-content:space-between;
    width: 160px;
    list-style: none;
    height: 14px;
    font-family: Avenir Next LT Pro;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.4px;
    color: #8C8C8C;
    padding: 10px 0 0 0;
}




