.grid-layout-100 {
    display: grid !important;
    grid-template-columns: repeat(1, 1fr);
}
.grid-layout-50 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
}

.sign-up-container {
    display:grid;
    grid-template-columns: 60% 35%;
    width:100%;
    z-index: 4;
    position: relative;
    top: -33px;
    
}
.sign-in-container {
    display:grid;
    grid-template-columns: 65% 35%;
    width:100%;
    z-index: 4;
    position: relative;
    top: 0;
}
.sign-up-form {
    padding:50px 70px;
    position: relative;
    left: 15px;
    border-radius: 15px;
    background-color: #fff;
}
.sign-in-form {
    padding:60px 70px;
    position: relative;
    left: 15px;
    border-radius: 15px;
    background-color: #fff;
    /* box-shadow: 2px 4px 8px #585858; */
}
.sign-up-form-header,
.sign-in-form-header {
    width: 376px;
    margin: auto;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.sign-up-form-header  h2,
.sign-in-form-header h2 {
font-family: 'Inter', sans-serif;
font-style: normal;
font-weight: bold;
width: 170px;
height: 40px;
font-size: 36px;
line-height: 110%;
padding-bottom: 10px;
color: #1C1C1C;
}
.sign-up-form-header span,
.sign-in-form-header span {
    font-family: Avenir Next LT Pro;
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 24px;
width: 210px;
height: 24px;
letter-spacing: 0.5px;
color: rgba(40, 44, 64, 0.6);

}
.sign-up-form-label,
.sign-in-form-label {
    width: 376px;
    height: 16px;
    margin: auto;
    padding: 7px 0;
    color: #82869A;
    font-size: 12px;
    font-weight: 600;
    font-family: Avenir Next LT Pro;
    font-style: normal;
    letter-spacing: 0.4px;
    margin-bottom: 10px;
}
.sign-up-form-input {
    padding: 10px 0;
    outline: none;
    border: none;
    width: 376px;
    margin:0 auto 20px auto ;
    border-bottom: 2px solid #8C8C8C;
    background-color: #fff !important;
}
 
.sign-in-form-input {
    padding: 10px 0;
    outline: none;
    border: none;
    width: 400px;
    margin:0 auto 20px auto ;
    border-bottom: 2px solid #8C8C8C;
    background-color: #fff !important;
}
small {
    color:red;
    margin-top: -23px;
    width: 376px;
    margin-left: auto;
    margin-right: auto;
}
