    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.25px;    
    color: #373C56;
}
.sign-up-form-input::placeholder,
.sign-in-form-input::placeholder {
    font-size: 14px;
    width: 166px;
    height: 20px;
    font-family: Avenir Next LT Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
