.sign-up-btn,
.sign-in-btn {
    background: #BD220B !important;
    border-color: #BD220B !important;
    border-radius: 8px;
    width: 376px;
    margin: 10px auto 0 auto;
    height: 55px;
    color:#fff;
    border: none;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.15);
    font-family: Avenir Next LT Pro;
    letter-spacing: 0.15px;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
}
.sign-up-btn:hover,
.sign-in-btn:hover {
    background: #BD220B;
}
.terms-pass-reset {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: auto;
    padding: 20px 0;
}
.sign-up-aside {
    background-color: black;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    background-image: url(/static/media/signup.4724c1c7.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.sign-in-aside {
    background-color: black;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    background-image: url(/static/media/signin.6a261026.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}



@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (-webkit-min-device-pixel-ratio: 1) {

.sign-up-container,
.sign-in-container {
    display:grid;
    grid-template-columns: repeat(1, 1fr);
    width:100%;
    height: 100vh;
    /* margin:auto  auto; */
    z-index: 4;
    /* position: relative; */
    top: 0;
    
}
.sign-up-aside,
.sign-in-aside,
.reset-pass-aside {
    display: none;
}

}
